* {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: all 0.3s;
}

body {
  background-color: $backColor;
  box-sizing: border-box;
}

html {
  overflow: auto;
}

.page {
  overflow-x: hidden;
}

.page:has(.menu:target) {
  overflow: hidden;
}
